body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.checklist {
  font-family: Arial, sans-serif;
  max-width: 500px;
  margin: 0 auto;
  padding: 20px;
  background-color: #f5f5f5;
  border-radius: 10px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
}

.checklist h1 {
  font-size: 24px;
  margin-bottom: 20px;
  text-align: center;
}

.checklist form {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.checklist label {
  margin-right: 10px;
}

.checklist input[type="text"] {
  padding: 10px;
  border: none;
  border-radius: 5px;
  flex-grow: 1;
}

.checklist .add-btn,
.checklist .delete-btn {
  padding: 10px;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

.checklist .add-btn {
  background-color: #28a745;
}

.checklist .add-btn:hover {
  background-color: #218838;
}

.checklist .delete-btn {
  background-color: #dc3545;
}

.checklist .delete:hover {
  cursor: pointer;
}
.checklist .checkbox:hover {
  cursor: pointer;
}

.checklist ul {
  margin: 0;
  padding: 0;
  justify-content: space-around;
}

.checklist li {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  align-items: center;
  padding: 4px;
  margin-bottom: 5px;
  background-color: white;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.checklist li.done {
  text-decoration: line-through;
  opacity: 0.5;
}
.checklist #deleteItems {
  -webkit-margin-start: auto;
          margin-inline-start: auto;
}
#deleteItems {
  color: #dc3545;
}
/* profile css */

.profile-page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.header {
  background-color: #333;
  color: #fff;
  padding: 1rem;
}

.header-container {
  max-width: 1200px;
  margin: 0 auto;
}

.profile-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 4rem;
  background-color: #eee;
}

.profile-picture {
  width: 200px;
  height: 200px;
  overflow: hidden;
  border-radius: 50%;
  margin-bottom: 1rem;
}

.profile-picture img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.profile-details {
  text-align: center;
}

.profile-details h2 {
  font-size: 3rem;
  margin-bottom: 0.5rem;
}

.profile-details h3 {
  font-size: 1.5rem;
  margin-bottom: 2rem;
}

.profile-details p {
  font-size: 1.2rem;
  line-height: 1.5;
  max-width: 800px;
  margin: 0 auto 2rem;
}

.profile-skills {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  max-width: 800px;
  margin: 0 auto;
  padding: 0;
  list-style: none;
}

.profile-skills li {
  background-color: #ddd;
  color: #333;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  margin: 0.5rem;
  font-size: 1.2rem;
}

.profile-skills li:first-child {
  margin-left: 0;
}

.profile-skills li:last-child {
  margin-right: 0;
}

.profile-section {
  padding: 4rem;
  background-color: #fff;
}

.profile-container {
  max-width: 1200px;
  margin: 0 auto;
}

.profile-section h2 {
  font-size: 2rem;
  margin-bottom: 2rem;
}

.profile-experience,
.profile-education {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.experience-card,
.education-card {
  background-color: #ddd;
  padding: 2rem;
  border-radius: 10px;
  margin: 2rem;
  width: 400px;
  height: 100%;
}

.experience-card h3,
.education-card h3 {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}

.experience-card h4,
.education-card h4 {
  font-size: 1.2rem;
  margin-bottom: 1rem;
}

.experience-card .experience-date,
.education-card .education-date {
  font-size: 1.2rem;
  margin-bottom: 1rem;
}

.experience-card p,
.education-card p {
  font-size: 1.2rem;
  line-height: 1.5;
}
.activated {
  display: inline-block !important ;
}
.inActive {
  display: none !important;
}
